import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import ApplicationSubjectListDetailCard from "./ApplicationSubjectListDetailCard";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useInView } from "react-intersection-observer";
import { useOneApplicationOneSubjectStudentDseList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
const ApplicationSubjectDseListDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);

  const {
    oneApplicationOneSubjectStudentDseList,
    oneApplicationOneSubjectStudentDseListLoading,
  } = useOneApplicationOneSubjectStudentDseList({
    data: {
      smid: getQuery.state?.smid,
      aid: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId
      ? getQuery.state?.smid
      : !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (oneApplicationOneSubjectStudentDseList?.list?.length) {
      let obj = nextPage(oneApplicationOneSubjectStudentDseList?.list?.length);
      setState(() => obj);
    }
  }, [oneApplicationOneSubjectStudentDseList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !oneApplicationOneSubjectStudentDseListLoading &&
      oneApplicationOneSubjectStudentDseList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  // const onExportList = () => {
  //   if (getQuery.state?.smid && getQuery.state?.appId) {
  //     setDisabled((pre) => !pre);
  //     oneApplicationOneSubjectStudentDseListExport({
  //       smid: getQuery.state?.smid,
  //       aid: getQuery.state?.appId,
  //       bodyParameter: {},
  //     })
  //       .then(() => {
  //         let url =
  //           getQuery.state?.urlOffSet === "Institute"
  //             ? `/q/${params.username}/institute/member/admission?type=export`
  //             : `/q/${params.username}/member/staff/admission?type=export`;
  //         navigate(url, {
  //           state: getQuery.state,
  //         });
  //       })
  //       .catch({});
  //   }
  // };
  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <div className={style.fm_header_container}>
          <h6>
            {t("student_list")} :{" "}
            <span
              style={{
                fontSize: "16px",
                color: "#0942a6",
              }}
            >
              {oneApplicationOneSubjectStudentDseList?.studentCount}
            </span>
          </h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {oneApplicationOneSubjectStudentDseList?.list?.map((student, index) =>
          oneApplicationOneSubjectStudentDseList?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <ApplicationSubjectListDetailCard student={student} />
            </div>
          ) : (
            <ApplicationSubjectListDetailCard
              student={student}
              key={student?._id}
            />
          )
        )}
        {oneApplicationOneSubjectStudentDseListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          oneApplicationOneSubjectStudentDseList?.list?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No students found related this search."
            />
          )
        ) : (
          oneApplicationOneSubjectStudentDseList?.list?.length === 0 && (
            <EmptyMessage
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
              title="No students."
            />
          )
        )}
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ApplicationSubjectDseListDetail;
