import { useTranslation } from "react-i18next";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import style from "../../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import ApplicationSubjectListDetail from "./ApplicationSubjectListDetail";
import ApplicationSubjectDseListDetail from "./ApplicationSubjectDseListDetail";
const ApplicationSubjectListDetailConfig = () => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.subjectName ?? ""}</h6>
          </div>
        </UserMemberContentWrapper>
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/admission?type=selected-subject${tab.urlKey}`
                  : `/q/${params.username}/member/staff/admission?type=selected-subject${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        {!searchParams.get("n") ? (
          <ApplicationSubjectListDetail />
        ) : (
          <ApplicationSubjectDseListDetail />
        )}
      </UserMemberContainerWrapper>
    </>
  );
};

export default ApplicationSubjectListDetailConfig;
const tabList = [
  {
    label: "normal_students",
    id: "normal",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "dse_student",
    id: "dse",
    urlKey: "&n=dse",
    groupIs: ["dse"],
  },
];
